<template>
    <div>
        <el-card>
            <div>
                <div id="arrangeIndex" style="font-size: 18px;">
                    <i
                        class="iconfont icon-r-list"
                        style="margin: 10px; font-size: 24px"
                    >
                    </i>
                    
                </div>
                <br />
                <ul class="dateUl">
                    <li v-for="monthDay in monthDays" :key="monthDay">
                        <el-button
                            type="primary"
                            style="margin: 5px;font-size: 18px;"
                            @click="dateClick(monthDay)"
                        >
                        <i class="iconfont icon-r-paper" style="font-size: 24px;"></i>
                            {{ monthDay }}</el-button
                        >
                    </li>
                </ul>
            </div>
            <div class="router-view">
                <router-view></router-view>
            </div>
        </el-card>
    </div>
</template>
<script>

import { getActivePath, setActivePath } from "@/utils/storage.js";
import { toLoad } from "@/utils/initialize.js";
const ARRDATE = "arrDate";
export default {
    name: "aOrderGroup",
    data() {
        return {
            monthDays: [],
            monthDay: "",
            activePath: "",
            
        };
    },

    methods: {
        //
        dateClick(monthDay) {
            console.log(monthDay);

            const nowDate = new Date();
            let year = nowDate.getFullYear();
            let dateTime = year + "-" + monthDay;
            sessionStorage.setItem(ARRDATE, dateTime);

            this.activePath = "asetmealChecks";
            setActivePath("asetmealChecks");
            if (this.$route.path !== "/asetmealChecks")
                this.$router.push("asetmealChecks");
        },
        //7
        nowDay(num) {
            var nowDate = new Date();
            nowDate.setDate(nowDate.getDate() + num);
            var month = nowDate.getMonth() + 1;
            var date = nowDate.getDate();
            if (date < 10) {
                date = "0" + date;
            }
            if (month < 10) {
                month = "0" + month;
            }
            var time = month + "-" + date;
            this.monthDays.push(time);
        },
        
    },
    mounted() {
        toLoad();
    },
    created() {
      
        //7
        for (var i = 0; i < 25; i++) {
            this.nowDay(i);
            //  
            this.activePath = getActivePath();
        }
       
    },
};
</script>
