<template>
    <el-card>
        <el-table :data="orderData" stripe border>
            <el-table-column label="" type="index" width="50">
            </el-table-column>
            <el-table-column label="" prop="oId"></el-table-column>
            <el-table-column label="id" prop="pId"></el-table-column>
            <el-table-column label="" prop="pName"></el-table-column>
            <el-table-column label="" prop="dName"></el-table-column>
            <el-table-column label="" prop="tname"></el-table-column>
            <el-table-column
                label=""
                prop="oStart"
                width="200px"
            ></el-table-column>
            <el-table-column label="" fixed="right">
                <template slot-scope="scope">
                    <el-button
                        type="warning"
                        style="font-size: 18px"
                        @click="dealClick(scope.row.oId, scope.row.pId,scope.row.chid,scope.row.pOid)"
                    >
                        <i
                            class="iconfont icon-r-love"
                            style="font-size: 26px"
                        ></i>
                         
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
    </el-card>
</template>
<script>
import { toLoad } from "@/utils/initialize.js";
import request from "@/utils/request.js";
import { getToken } from "@/utils/storage.js";
import jwtDecode from "jwt-decode";
export default {
    name: "setmealOrderToday",
    data() {
        return {
            userId: 1,
            userName: "dada",
            today: "",

            orderData: [],
        };
    },
    methods: {
        ////
        dealClick(oId, pId,chid,pOid) {
            this.$router.push({
                path: "/dealOrder",
                query: {
                    oId: oId,
                    pId: pId,
                    chid:chid,
                    otype:'TT',
                    pOid:pOid
                },
            });
        },
        //
        requestOrder() {
            console.log(this.today)
            request
                .get("doctor/findOrderByNull", {
                    params: {
                        dId: this.userId,
                        oStart: this.today,
                        otype:'TT',
                    },
                })
                .then((res) => {
                    toLoad();
                    if (res.data.status != 200)
                        return this.$message.error("");
                    this.orderData = res.data.data;
                });
        },
        //token
        tokenDecode(token) {
            if (token !== null) return jwtDecode(token);
        },
        //
        nowDay() {
            const nowDate = new Date();
            let date = {
                year: nowDate.getFullYear(),
                month: nowDate.getMonth() + 1,
                date: nowDate.getDate(),
            };
            if (date.date < 10) {
                date.date = "0" + date.date;
            }
            if (date.month < 10) {
                date.month = "0" + date.month;
            }
            this.today = date.year + "-" + date.month + "-" + date.date;
        },
    },
    created() {
        //token
        this.userId = this.tokenDecode(getToken()).dId;
        this.userName = this.tokenDecode(getToken()).dName;
        console.log(this.userId);
        console.log(this.userName);
        //
        this.nowDay();
        //
        this.requestOrder();
    },
};
</script>