<template>
    <div>
        <!--  -->
        <el-card>
            <!--  -->
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/orderOperate' }"
                    ></el-breadcrumb-item
                >
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>

            <!--  -->
            <div class="head">
                <div>
                    <i
                        class="iconfont icon-r-user1"
                        style="margin: 5px; font-size: 26px"
                    >
                        {{ sectionOpt }}</i
                    >
                </div>

                <!--  -->
                <div>
                    <i class="iconfont icon-r-paper" style="font-size: 22px">
                        </i
                    >
                    <ul class="dateUl">
                        <li v-for="monthDay in monthDays" :key="monthDay">
                            <el-button
                                icon="iconfont icon-r-paper"
                                @click="dateClick(monthDay)"
                            >
                                {{ monthDay }}</el-button
                            >
                        </li>
                    </ul>
                </div>
            </div>

            <!--  -->
            <el-table :data="sectionData" stripe style="width: 100%" border>
                <el-table-column
                    type="index"
                    label=""
                    width="60"
                ></el-table-column>
                <el-table-column prop="dId" label="" width="80">
                </el-table-column>
                <el-table-column prop="dName" label="" width="80">
                </el-table-column>
                <el-table-column prop="dGender" label="" width="60">
                </el-table-column>
                <el-table-column prop="dPost" label="" width="100">
                </el-table-column>
                <el-table-column
                    prop="dSection"
                    label=""
                    width="100"
                ></el-table-column>
                <el-table-column prop="dIntroduction" label="">
                </el-table-column>
                <!--<el-table-column prop="dPrice" label="/" width="80">
                </el-table-column>-->
                <el-table-column prop="dAvgStar" label="/5" width="80">
                </el-table-column>
                <el-table-column label="" width="140" v-if="clickTag">
                    <template slot-scope="scope">
                        <el-button
                            class="iconfont icon-r-paper"
                            style="font-size: 14px"
                            type="warning"
                            @click="openClick(scope.row.dId, scope.row.dName,scope.row.dPrice,scope.row.dSection)"
                        >
                            </el-button
                        >
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="orderFormVisible">
            <el-form :model="orderForm" ref="orderForm" :rules="orderRules">
                <el-form-item
                    label=""
                    label-width="100px"
                    prop="oTime"
                >
                    <el-select
                        v-model="orderForm.oTime"
                        placeholder=""
                        no-data-text=""
                    >
                        <el-option
                            v-for="time in times"
                            :key="time"
                            :label="time"
                            :value="time"
                        >
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="" label-width="100px" prop="chName">
                    <el-select @change="changcheck(modifyForm)"
                        v-model="modifyForm.chName"
                        placeholder="2"
                    >
                        <el-option
                            v-for="post in posts"
                            :key="post.chName"
                            :label="post.chName"
                            :value="post"
                        >
                        </el-option>
                    </el-select>
                </el-form-item>     


                <el-form-item label="" label-width="100px">
                    <el-input
                        v-model="orderForm.orderDate"
                        autocomplete="off"
                        disabled
                    ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input
                        v-model="orderForm.dId"
                        autocomplete="off"
                        disabled
                    ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input
                        v-model="orderForm.dName"
                        autocomplete="off"
                        disabled
                    ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input
                        v-model="orderForm.pName"
                        autocomplete="off"
                        disabled
                    ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input
                        v-model="orderForm.pCard"
                        autocomplete="off"
                        disabled
                    ></el-input>
                </el-form-item>
                <el-form-item label="/" label-width="100px">
                    <el-input
                        v-model="orderForm.dPrice"
                        autocomplete="off"
                        disabled
                    ></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button
                    @click="orderFormVisible = false"
                    style="font-size: 18px"
                    ><i
                        class="iconfont icon-r-left"
                        style="font-size: 20px"
                    ></i>
                     </el-button
                >
                <el-button
                    type="primary"
                    @click="orderSuccess('orderForm')"
                    style="font-size: 18px"
                    ><i class="iconfont icon-r-yes" style="font-size: 20px"></i>
                     </el-button
                >
            </div>
        </el-dialog>
    </div>
</template>
<script>
import request from "@/utils/request.js";
import { getToken } from "@/utils/storage.js";
import jwtDecode from "jwt-decode";
export default {
    name: "sectionMessage",
    data() {
        return {
            sectionOpt: this.$route.query.sectionOpt,
            sectionData: [],
            monthDays: [],
            dSection:"",
            posts: ["", "", "", "", "", "", ""],
            clickTag: false,
            oTotalPrice:"",
            orderFormVisible: false,
            orderForm: { orderDate: "" },
            times: [],
            orderRules: {
                oTime: [
                    { required: true, message: "", trigger: "blur" },
                ],
            },
            //
            orderDate: "",
            //oId
            idTime: "",
            modifyForm:{},
        };
    },
    methods: {
        changcheck(modifyForm){
            console.log(modifyForm)
            this.orderForm.dPrice =this.modifyForm.chName.chPrice
        },

        requestChecksss(){
            request
                .get("check/findChecksBykname", {
                    params: {
                        kname: this.dSection,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        return this.$message.error("");
                    this.posts = res.data.data;

                    
                });


        },

        //,
        requestTime(id) {
            this.requestChecksss();
            this.idTime = id + this.orderDate;
            request
                .get("order/findOrderTime", {
                    params: {
                        arId: this.idTime,
                    },
                })
                .then((res) => {
                    const date = new Date(this.orderDate);
                    const today = new Date();
                    const isToday =
                        date.getFullYear() === today.getFullYear() &&
                        date.getMonth() === today.getMonth() &&
                        date.getDate() === today.getDate();
                    var array = [];
                   
                    if (!this.isTimeAfterTarget("11:30") || !isToday) {
                        array.push(
                            "08:30-11:30  " + "    " + res.data.data.eTOn
                        );
                    }
                
                    if (!this.isTimeAfterTarget("17:30") || !isToday) {
                        array.push(
                            "14:30-17:30  " + "    " + res.data.data.fTOf
                        );
                    }
                    this.times = array;
                });
        },
        isTimeAfterTarget(timeString) {
            // timeString(09:30)
            const currentTime = new Date();

            // 
            const [targetHour, targetMinute] = timeString.split(":");

            // 
            const targetTime = new Date();
            targetTime.setHours(targetHour);
            targetTime.setMinutes(targetMinute);
            targetTime.setSeconds(0);

            // 
            return currentTime > targetTime;
        },
        //
        orderSuccess(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .get("patient/addOrder", {
                            params: {
                                pId: this.orderForm.pId,
                                dId: this.orderForm.dId,
                                oTotalPrice: this.modifyForm.chName.chPrice,
                                oStart:
                                    this.orderForm.orderDate +
                                    " " +
                                    this.orderForm.oTime,
                                arId: this.idTime,
                                tname:this.modifyForm.chName.chName,
                            },
                        })
                        .then((res) => {
                            if (res.data.status != 200)
                                return this.$message.error(
                                    ""
                                );
                            this.orderFormVisible = false;
                            this.$message.success("");
                            this.orderForm.oTime = "";
                        })
                        .catch((e) => {
                            if (e.response.data == undefined) {
                                this.$message.error(e);
                            } else {
                                this.$message.error(e.response.data);
                            }
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //token
        tokenDecode(token) {
            if (token !== null) return jwtDecode(token);
        },
        //
        openClick(id, name,dPrice,dSection) {
            this.dSection=dSection;
            this.orderForm.dPrice = dPrice;
            this.orderForm.dId = id;
            this.orderForm.dName = name;
            this.orderFormVisible = true;
            //
            this.requestTime(id);
        },
        //
        dateClick(date) {
            //
            const nowDate = new Date();
            let year = nowDate.getFullYear();
            this.orderForm.orderDate = year + "-" + date;
            let dateYear = year + "-" + date;
            this.orderDate = dateYear;
            request
                .get("/arrange/findByTime", {
                    params: {
                        arTime: dateYear,
                        dSection: this.sectionOpt,
                    },
                })
                .then((res) => {
                    //this.sectionData.dId = res.data.data.doctors.dId;
                    /**
                     * map
                     */
                    this.sectionData = res.data.data.map((item) => item.doctor);
                    this.clickTag = true;
                    console.log(res.data.data.map((item) => item.doctor));
                    //console.log(res.data.data[0].doctor);
                });
        },
        //7
        nowDay(num) {
            var nowDate = new Date();
            var currentHour = nowDate.getHours();
            var currentMinute = nowDate.getMinutes();

            // 17:30
            if (
                currentHour > 17 ||
                (currentHour === 17 && currentMinute > 30)
            ) {
                num++; // 
            }

            nowDate.setDate(nowDate.getDate() + num);
            var month = nowDate.getMonth() + 1;
            var date = nowDate.getDate();
            if (date < 10) {
                date = "0" + date;
            }
            if (month < 10) {
                month = "0" + month;
            }
            var time = month + "-" + date;
            this.monthDays.push(time);
        },
        //
        requestSection() {
            request
                .get("patient/findDoctorBySection", {
                    params: {
                        dSection: this.$route.query.sectionOpt,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        return this.$message.error("");
                    this.sectionData = res.data.data.doctors;
                    console.log(res.data.data.doctors);
                });
        },
    },
    created() {
        //7
        for (var i = 0; i < 7; i++) {
            this.nowDay(i);
        }
        //
        this.requestSection();
        // token
        this.orderForm.pName = this.tokenDecode(getToken()).pName;
        this.orderForm.pCard = this.tokenDecode(getToken()).pCard;
        this.orderForm.pId = this.tokenDecode(getToken()).pId;
        //console.log(this.orderForm.pId)
    },
};
</script>
