<template>
<div>
  <el-card>
    <el-row>
      <el-col :span="7">
          <el-input disabled v-model="oId" class="orderInput"></el-input>
      </el-col>
      <el-col :span="7">
          <el-input disabled v-model="pId" class="orderInput"></el-input></el-col>
      <el-col :span="7">
          <el-input disabled v-model="pName" class="orderInput"></el-input>
      </el-col>
        <el-col :span="3">
          <el-button type="success" 
                  style="font-size: 18px;"
                  icon="iconfont icon-r-paper" @click="submitClick"> </el-button>
      </el-col>
    </el-row>
    <el-row>
          <el-col :span="7">
          <el-input disabled v-model="pGender" class="orderInput"></el-input>
      </el-col>
      <el-col :span="7">
          <el-input disabled v-model="pPhone" class="orderInput"></el-input></el-col>
      <el-col :span="7">
          <el-input disabled v-model="dName" class="orderInput"></el-input>
      </el-col>
              <el-col :span="3">
          <el-button type="success" 
                  style="font-size: 18px;" @click="openAdvice">
                  <i class="iconfont icon-r-add" style="font-size: 20px;"></i> 
                  /</el-button>
      </el-col>
    </el-row>
      <!--  -->
      <el-row>
        <el-col :span="12">
          <el-input v-model="queryDrug" placeholder="" class="drugInput">
            <el-button
            slot="append"
            icon="el-icon-search"
            @click="requestDrug"
          ></el-button>
          </el-input>
      <el-table :data="drugData" stripe border>
        <el-table-column label="" prop="drId"></el-table-column>
        <el-table-column label="" prop="drName"></el-table-column>
        <el-table-column label="" prop="drNumber"></el-table-column>
        <el-table-column label="" prop="drUnit"></el-table-column>
        <el-table-column label="" prop="drPrice"></el-table-column>
        <el-table-column label="" width="120" fixed="right">
          <template slot-scope="scope">
            <el-button type="success" 
                  style="font-size: 14px;" @click="addDrug(scope.row.drId)">
                  <i class="iconfont icon-r-add" style="font-size: 16px;"></i> 
                  </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
  background
  layout="total, sizes, prev, pager, next"
  :total="total"
  :page-size="size"
  :page-sizes="[1, 2, 4, 8, 16]"
  >
  
</el-pagination>
<el-row></el-row>
      </el-col>
      <!--  -->
      <el-col :span="12" class="drugRigth">
        <el-table stripe border :data="drugBuyData" class="rigthTable">
          <el-table-column label="" prop="drId"></el-table-column>
          <el-table-column label="" prop="drName"></el-table-column>
           <el-table-column label="" prop="drPrice"></el-table-column>
            <el-table-column label="" prop="drNum"></el-table-column>
             <el-table-column label="" prop="drSum"></el-table-column>
             <el-table-column label="" width="120" fixed="right">
               <template slot-scope="scope">
                 <el-button type="danger" 
                  style="font-size: 14px;"
                  icon="iconfont icon-r-delete" @click="deleteDrug(scope.row.drId)"> </el-button>
               </template>
             </el-table-column>
        </el-table>
        <el-tag>{{drugTotalPrice}}</el-tag>

      </el-col>
      
</el-row>
<!--  -->
<el-row>
      <el-col :span="12">
        <el-input v-model="queryCheck" placeholder="" class="drugInput">
            <el-button
            slot="append"
            icon="el-icon-search"
            @click="requestCheck"
          ></el-button>
          </el-input>
          <el-table stripe border :data="checkData">
            <el-table-column label="" prop="chId"></el-table-column>
             <el-table-column label="" prop="chName"></el-table-column>
              <el-table-column label="" prop="chPrice"></el-table-column>
              <el-table-column label="" width="120" fixed="right">
                <template slot-scope="scope">
                  <el-button type="success" 
                  style="font-size: 14px;" @click="addCheck(scope.row.chId)">
                  <i class="iconfont icon-r-add" style="font-size: 16px;"></i> 
                  </el-button>
                </template>
              </el-table-column>
          </el-table>
                <!--  -->
      <el-pagination
      @size-change="checkSizeChange"
      @current-change="checkCurrentChange"
  background
  layout="total, prev, pager, next"
  :total="checkTotal"
  :page-size="checkSize"
  >
      </el-pagination>
      </el-col>
      <!--  -->
      <el-col :span="12" class="drugRigth">
        <el-table stripe border class="rigthTable" :data="checkBuyData">
          <el-table-column label="" prop="chId"></el-table-column>
             <el-table-column label="" prop="chName"></el-table-column>
              <el-table-column label="" prop="chPrice"></el-table-column>
              <el-table-column label="" width="120" fixed="right">
                <template slot-scope="scope">
                  <el-button type="danger" 
                  style="font-size: 14px;"
                  icon="iconfont icon-r-delete" @click="deleteCheck(scope.row.chId)"> </el-button>
                </template>
              </el-table-column>
              </el-table>
              <el-tag>{{checkTotalPrice}}</el-tag>
      </el-col>
    </el-row>

  </el-card>


<!--  -->
 <el-dialog title="" :visible.sync="adviceFormVisible">
   <el-input
  type="textarea"
  :rows="8"
  placeholder=""
  v-model="advice">
</el-input>
<div slot="footer" class="dialog-footer">
        <el-button @click="adviceFormVisible = false" style="font-size: 18px;"><i class="iconfont icon-r-left" style="font-size: 20px;"></i>  </el-button>
        <el-button type="primary" @click="holdAdvice"
          ></el-button
        >
      </div>
 </el-dialog>

  </div>
</template>
<script>
import request from "@/utils/request.js";
import { getToken } from "@/utils/storage.js";
import jwtDecode from "jwt-decode";
export default {
  name: "dealOrderAgain",
  data() {
      return {
          oId: 1,
          pId: 1,
          dId: 0,
          pName: "aa",
          pGender: "as",
          pPhone: "asd",
          dName: "",
          drugData:[],
          size: 4,
          pageNumber: 1,
          total: 3,
          queryDrug: "",
          drugBuyData: [],
          drugTotalPrice:0,
          //
          advice: "",
          //
          checkData:[],
          queryCheck:"",
          checkTotal:3,
          checkSize:4,
          checkPageNumber:1,
          checkTotalPrice:0,
          checkBuyData:[],
          // 
          adviceFormVisible: false,

      }
  },
  methods: {
    //id
    reduceDrugNumber(drId,usedNumber){
      request.get("drug/reduceDrugNumber", {
        params: {
          drId: drId,
          usedNumber: usedNumber,
        }
      })
      .then(res => {
       if(res.data.status !== 200){
         this.$message.error("");
         }
      })
    },
    //
    submitClick(){
      
      for(let i=0;i<this.drugBuyData.length;i++){
         this.reduceDrugNumber(this.drugBuyData[i].drId, this.drugBuyData[i].drNum);
      }; 
      let data = { 
          oId: this.dataPackage().oId,
        //   pId: this.pId,
        //   dId: this.dId,
          oAdvice: this.dataPackage().oAdvice,
          oDrug: this.dataPackage().oDrug,
          oCheck: this.dataPackage().oCheck,
          oTotalPrice: this.dataPackage().oTotalPrice }
      request.post("order/updateOrderByAdd",data)
      .then(res => {
          console.log(res)
              if(res.data.status !== 200){
         this.$message.error("");
         return;
              }
              
         //this.$message.success("");
                  request.get("order/findTotalPrice", {
           params: {
             oId: data.oId
           }
         })
         .then(res => {
           if(res.data.status === 200)
           this.$message.success("");
           
         });



         this.$router.push("/orderToday");
         console.log(res.data)
      })
      .catch(err => {
        console.error(err); 
      })

    },
    //
    dataPackage(){
      let oDrug = " // ";
      let oCheck = " // ";
      let oTotalPrice = 0;
      let oId = this.oId;
      let oAdvice = this.advice;
      for(let i=0;i<this.drugBuyData.length;i++){
        oDrug += this.drugBuyData[i].drName+"*"+this.drugBuyData[i].drPrice+"()*"+this.drugBuyData[i].drNum+" ";
      };
       for(let i=0;i<this.checkBuyData.length;i++){
        oCheck += this.checkBuyData[i].chName+"*"+this.checkBuyData[i].chPrice+"() ";
      }
      oCheck += ""+this.checkTotalPrice+"";
      oDrug += ""+this.drugTotalPrice+"";
      oTotalPrice = this.checkTotalPrice+this.drugTotalPrice;
      return {oId,oAdvice, oDrug, oCheck, oTotalPrice};

    },
    //
    holdAdvice(){
      this.adviceFormVisible = false;
      this.$message.success("");
    },
    //
    openAdvice(){
      this.adviceFormVisible = true;

    },
      //
    deleteCheck(chId){
      for(let i=0;i<this.checkBuyData.length;i++){
        if(this.checkBuyData[i].chId === chId){
          this.checkTotalPrice -= this.checkBuyData[i].chPrice;//
          this.checkBuyData.splice(i,1) ;//i
        }
      }
    },
      //
    addCheck(chId){
      request.get("check/findCheck", {
        params: {
          chId: chId
        }
      })
      .then(res => {
        if(res.data.status != 200)
        this.$message.error("");
        //
           this.checkBuyData.push(
          {
            chId: res.data.data.chId,
            chPrice: res.data.data.chPrice,
            chName:res.data.data.chName,
          }
          )
           this.checkTotalPrice += res.data.data.chPrice;//
        console.log(res.data.data);
      })

    },
        //
    checkSizeChange(size){
      this.checkSize = size;
      this.requestCheck();

    },
    //
    checkCurrentChange(num){
      this.checkPageNumber = num;
      this.requestCheck();
    },
    //
    requestCheck(){
      request.get("check/findAllChecks", {
        params: {
          size: this.checkSize,
          pageNumber: this.checkPageNumber,
          query: this.queryCheck
        }
      })
      .then(res => {
        if(res.data.status != 200)
              this.$message.error("");
              this.checkData = res.data.data.checks;
              this.checkTotal = res.data.data.total;
        console.log(res);
      })
    },
    /**
     * 
     */
    //
    deleteDrug(drId){
      for(let i=0;i<this.drugBuyData.length;i++){
        if(this.drugBuyData[i].drId === drId){
          for(let j=0;j<this.drugData.length;j++){
            if(this.drugData[j].drId === drId)
            this.drugData[j].drNumber +=1;
          }
          this.drugBuyData[i].drNum-=1;
          this.drugBuyData[i].drSum = this.drugBuyData[i].drPrice * this.drugBuyData[i].drNum;
          this.drugTotalPrice -= this.drugBuyData[i].drPrice;//
          if(this.drugBuyData[i].drNum === 0)
          this.drugBuyData.splice(i,1) ;//i
        }
      }
    },
    //
    addDrug(drId){
      request.get("drug/findDrug", {
        params: {
          drId: drId
        }
      })
      .then(res => {
        if(res.data.status != 200)
        this.$message.error("");
        //
        for(let i=0;i<this.drugBuyData.length;i++){
          if(this.drugBuyData[i].drId === res.data.data.drId){
          for(let j=0;j<this.drugData.length;j++){
            if(this.drugData[j].drId === res.data.data.drId && this.drugData[j].drNumber > 0){
            this.drugData[j].drNumber -=1;
            this.drugBuyData[i].drNum+=1;
          this.drugBuyData[i].drSum = this.drugBuyData[i].drPrice * this.drugBuyData[i].drNum;
          this.drugTotalPrice += this.drugBuyData[i].drPrice;//
          //return;
            }
          }
          return;
          }
        }
        for(let j=0;j<this.drugData.length;j++){
            if(this.drugData[j].drId === res.data.data.drId && this.drugData[j].drNumber <= 0)
            return;
          }
           this.drugBuyData.push(
          {
            drId: res.data.data.drId,
            drPrice: res.data.data.drPrice,
            drName:res.data.data.drName,
            drNum: 1,
            drSum: res.data.data.drPrice,
          }
          )
          for(let j=0;j<this.drugData.length;j++){
            if(this.drugData[j].drId === res.data.data.drId )
            this.drugData[j].drNumber -=1;
          }
           this.drugTotalPrice += res.data.data.drPrice;//
         

        console.log(res.data.data);
      })

    },
    //
    handleSizeChange(size){
      this.size = size;
      this.requestDrug();

    },
    //
    handleCurrentChange(num){
      this.pageNumber = num;
      this.requestDrug();
    },

    //
    requestDrug(){
      request.get("drug/findAllDrugs", {
        params: {
          size: this.size,
          pageNumber: this.pageNumber,
          query: this.queryDrug
        }
      })
      .then(res => {
         if(res.data.status != 200)
              this.$message.error("");
              this.drugData = res.data.data.drugs;
              this.total = res.data.data.total;
  

        console.log(res);
      })
    },
      //
      requestPatient(){
          request.get("doctor/findPatientById", {
              params: {
                  pId: this.pId
              }
          })
          .then(res => {
            console.log(res);
              if(res.data.status != 200)
              this.$message.error("");
              this.pName = res.data.data.pName;
              this.pGender = res.data.data.pGender;
              this.pPhone = res.data.data.pPhone;
              
          })

      },
    //token
    tokenDecode(token){
      if (token !== null)
      return jwtDecode(token);
    },
  },
  created() {
    this.dName = this.tokenDecode(getToken()).dName;
    this.dId = this.tokenDecode(getToken()).dId;
    this.oId = this.$route.query.oId;
    this.pId = this.$route.query.pId;
    this.requestPatient();
    this.requestDrug();
    this.requestCheck();
  },
};
</script>
<style lang="scss">

// 
.drugRigth{
  text-align: center;
}
.el-tag{
  margin: 8px;
}
.rigthTable{
  margin-top: 56px;
  margin-left: 8px;
}
.drugInput{
  margin-top: 8px;
  margin-bottom: 8px;
}
