<script>
import { toLoad } from "@/utils/initialize.js";
import request from "@/utils/request.js";
import { getToken } from "@/utils/storage.js";
import jwtDecode from "jwt-decode";
export default {
  name: "dealOrder",
  data() {
    return {
      updte:{
        sg:"",
        tz:"",
        xy:"",
        xl:"",
        tl:"",
        zr:"",
        yr:"",
        xj:"",
        kc:"",
        zy:"",
        yy:"",
        jzy:"",
        jyy:"",
        sj:"",
        jzx:"",
        lb:"",
        ggn:"",
        ygn:"",
        xdt:"",
        xn:"",
      },


      oId: 1,
      pId: 1,
      dId: 0,
      pName: "aa",
      pGender: "as",
      pPhone: "asd",
      dName: "",
      drugData: [],
      size: 4,
      pageNumber: 1,
      total: 3,
      queryDrug: "",
      drugBuyData: [],
      drugTotalPrice: 0,
      chid:"",
      otype:"",
      oAdvice:"",
      pOid:"",
      //
      reason: "",
      //
      checkData: [],
      queryCheck: "",
      checkTotal: 3,
      checkSize: 4,
      checkPageNumber: 1,
      checkTotalPrice: 0,
      checkBuyData: [],
      // 
      reasonFormVisible: false,
    };
  },
  methods: {
    //id
    reduceDrugNumber(drId, usedNumber) {
      request
        .get("drug/reduceDrugNumber", {
          params: {
            drId: drId,
            usedNumber: usedNumber,
          },
        })
        .then((res) => {
          if (res.data.status !== 200) {
            this.$message.error("");
          }
        });
    },
    //
    submitClick() {
      for (let i = 0; i < this.drugBuyData.length; i++) {
        this.reduceDrugNumber(
          this.drugBuyData[i].drId,
          this.drugBuyData[i].drNum
        );
      }
      let data = {
        oId: this.dataPackage().oId,
        pId: this.pId,
        dId: this.dId,
        oRecord: this.dataPackage().oRecord,
        oDrug: this.dataPackage().oDrug,
        oCheck: this.dataPackage().oCheck,
        oTprice: this.dataPackage().oTotalPrice,
        otype:this.otype,
        pOid:this.pOid,
        oAdvice:this.oAdvice,
        sg:this.updte.sg,
        tz:this.updte.tz,
        xy:this.updte.xy,
        xl:this.updte.xl,
        tl:this.updte.tl,
        zr:this.updte.zr,
        yr:this.updte.yr,
        xj:this.updte.xj,
        kc:this.updte.kc,
        zy:this.updte.zy,
        yy:this.updte.yy,
        jzy:this.updte.jzy,
        jyy:this.updte.jyy,
        sj:this.updte.sj,
        jzx:this.updte.jzx,
        lb:this.updte.lb,
        ggn:this.updte.ggn,
        ygn:this.updte.ygn,
        xdt:this.updte.xdt,
        xn:this.updte.xn,
 
      };
      request
        .post("order/updateOrder", data)
        .then((res) => {
          if (res.data.status !== 200) {
            this.$message.error("");
            return;
          }
          this.$message.success("");
          if('TT'==this.otype){
            this.$router.push("setmealOrderToday");
          }else{
            this.$router.push("/orderToday");
          }
          
          console.log(res.data);
        })
        .catch((err) => {
          console.error(err);
        });
    },
    //
    dataPackage() {
      let oDrug = "";
      let oCheck = "";
      let oTotalPrice = 0;
      let oId = this.oId;
      let oRecord = this.reason;
      for (let i = 0; i < this.drugBuyData.length; i++) {
        oDrug +=
          this.drugBuyData[i].drName +
          "*" +
          this.drugBuyData[i].drPrice +
          "()*" +
          this.drugBuyData[i].drNum +
          " ";
      }
      for (let i = 0; i < this.checkBuyData.length; i++) {
        oCheck +=
          this.checkBuyData[i].chName +
          "*" +
          this.checkBuyData[i].chPrice +
          "() ";
      }
      oCheck += " " + this.checkTotalPrice + " ";
      oDrug += " " + this.drugTotalPrice + " ";
      oTotalPrice = this.drugTotalPrice;
      return { oId, oRecord, oDrug, oCheck, oTotalPrice };
    },
    //
    holdReason() {
      this.reasonFormVisible = false;
      this.$message.success("");
    },
    //
    openReason() {
      this.reasonFormVisible = true;
    },
    //
    deleteCheck(chId) {
      for (let i = 0; i < this.checkBuyData.length; i++) {
        if (this.checkBuyData[i].chId === chId) {
          this.checkTotalPrice -= this.checkBuyData[i].chPrice; //
          this.checkBuyData.splice(i, 1); //i
        }
      }
    },
    //
    addCheck(chId) {
      request
        .get("check/findCheck", {
          params: {
            chId: chId,
          },
        })
        .then((res) => {
          if (res.data.status != 200) return this.$message.error("");
          //
          this.checkBuyData.push({
            chId: res.data.data.chId,
            chPrice: res.data.data.chPrice,
            chName: res.data.data.chName,
          });
          this.checkTotalPrice += res.data.data.chPrice; //
          console.log(res.data.data);
        });
    },
    //
    checkSizeChange(size) {
      this.checkSize = size;
      this.requestCheck();
    },
    //
    checkCurrentChange(num) {
      this.checkPageNumber = num;
      this.requestCheck();
    },
    //
    requestCheck() {
      
      request
        .get("check/findAllChecks", {
          params: {
            size: this.checkSize,
            pageNumber: this.checkPageNumber,
            query: this.queryCheck,
            chId: this.chid,
          },
        })
        .then((res) => {
          toLoad();
          if (res.data.status != 200) this.$message.error("");
          this.checkData = res.data.data.checks;
          this.checkTotal = res.data.data.total;
          console.log(res);
        });
    },
    /**
     * 
     */
    //
    deleteDrug(drId) {
      for (let i = 0; i < this.drugBuyData.length; i++) {
        if (this.drugBuyData[i].drId === drId) {
          for (let j = 0; j < this.drugData.length; j++) {
            if (this.drugData[j].drId === drId) this.drugData[j].drNumber += 1;
          }
          this.drugBuyData[i].drNum -= 1;
          this.drugBuyData[i].drSum =
            this.drugBuyData[i].drPrice * this.drugBuyData[i].drNum;
          this.drugTotalPrice -= this.drugBuyData[i].drPrice; //
          if (this.drugBuyData[i].drNum === 0) this.drugBuyData.splice(i, 1); //i
        }
      }
    },
    //
    addDrug(drId) {
      request
        .get("drug/findDrug", {
          params: {
            drId: drId,
          },
        })
        .then((res) => {
          if (res.data.status != 200) return this.$message.error("");
          //
          for (let i = 0; i < this.drugBuyData.length; i++) {
            if (this.drugBuyData[i].drId === res.data.data.drId) {
              for (let j = 0; j < this.drugData.length; j++) {
                if (
                  this.drugData[j].drId === res.data.data.drId &&
                  this.drugData[j].drNumber > 0
                ) {
                  this.drugData[j].drNumber -= 1;
                  this.drugBuyData[i].drNum += 1;
                  this.drugBuyData[i].drSum =
                    this.drugBuyData[i].drPrice * this.drugBuyData[i].drNum;
                  this.drugTotalPrice += this.drugBuyData[i].drPrice; //
                  //return;
                }
              }
              return;
            }
          }
          for (let j = 0; j < this.drugData.length; j++) {
            if (
              this.drugData[j].drId === res.data.data.drId &&
              this.drugData[j].drNumber <= 0
            )
              return;
          }
          this.drugBuyData.push({
            drId: res.data.data.drId,
            drPrice: res.data.data.drPrice,
            drName: res.data.data.drName,
            drNum: 1,
            drSum: res.data.data.drPrice,
          });
          for (let j = 0; j < this.drugData.length; j++) {
            if (this.drugData[j].drId === res.data.data.drId)
              this.drugData[j].drNumber -= 1;
          }
          this.drugTotalPrice += res.data.data.drPrice; //

          console.log(res.data.data);
        });
    },
    //
    handleSizeChange(size) {
      this.size = size;
      this.requestDrug();
    },
    //
    handleCurrentChange(num) {
      this.pageNumber = num;
      this.requestDrug();
    },

    //
    requestDrug() {
      request
        .get("drug/findAllDrugs", {
          params: {
            size: this.size,
            pageNumber: this.pageNumber,
            query: this.queryDrug,
          },
        })
        .then((res) => {
          if (res.data.status != 200) this.$message.error("");
          this.drugData = res.data.data.drugs;
          this.total = res.data.data.total;

          console.log(res);
        });
    },
    //
    requestPatient() {
      request
        .get("doctor/findPatientById", {
          params: {
            pId: this.pId,
          },
        })
        .then((res) => {
          console.log(res);
          if (res.data.status != 200) this.$message.error("");
          this.pName = res.data.data.pName;
          this.pGender = res.data.data.pGender;
          this.pPhone = res.data.data.pPhone;
        });
    },
    //token
    tokenDecode(token) {
      if (token !== null) return jwtDecode(token);
    },
  },
  created() {
    this.dName = this.tokenDecode(getToken()).dName;
    this.dId = this.tokenDecode(getToken()).dId;
    this.oId = this.$route.query.oId;
    this.pId = this.$route.query.pId;
    this.chid = this.$route.query.chid;
    this.otype = this.$route.query.otype;
    this.pOid =this.$route.query.pOid
    this.requestPatient();
    this.requestDrug();
    this.requestCheck();
  },
};
</script>
