                    width="75px"
                ></el-table-column>
                <el-table-column
                    prop="pName"
                    label=""
                    width="75px"
                ></el-table-column>
                <el-table-column
                    prop="dId"
                    label="id"
                    width="75px"
                ></el-table-column>
                <el-table-column
                    prop="dName"
                    label=""
                    width="75px"
                ></el-table-column>
                <el-table-column
                    prop="tname"
                    label=""
                    width="75px"
                ></el-table-column>  

                <el-table-column
                    prop="oStart"
                    label=""
                    width="195px"
                ></el-table-column>
                <el-table-column
                    prop="oEnd"
