                ></el-table-column>
                <el-table-column
                    prop="oTotalPrice"
                    label="/"
                    width="80px"
                ></el-table-column>
                <el-table-column
                    prop="oPriceState"
                    label=""
                    width="150"
                >
                    <template slot-scope="scope">
                        <el-tag
                            type="success"
                            v-if="scope.row.oPriceState === 1"
                            ></el-tag
                        >
                        <el-tag
                            type="success"
                            v-if="scope.row.oPriceState === 0 &&
                                scope.row.oState === 0"
                            ></el-tag
                        >
                        <!-- <el-tag type="danger" v-if="scope.row.oPriceState === 0 && scope.row.oState === 1"></el-tag> -->
                        <el-button
                            type="warning"
                            icon="iconfont icon-r-mark1"
                            style="font-size: 14px"
                            v-if="
                                scope.row.oPriceState === 0 &&
                                scope.row.oState === 1
                            "
                            @click="priceClick(scope.row.oId, scope.row.dId, scope.row.tid,scope.row.otype)"
                        >
                            </el-button
                        >
                    </template>
                </el-table-column>
                <el-table-column
                    prop="oDrug"
                    label=""
                    width="150"
                >
                   
                </el-table-column> 
                <el-table-column
                    prop="oTprice"
                    label="/"
                    width="80px"
                ></el-table-column>   
                <el-table-column
                    prop="oPstate"
                    label=""
                    width="150"
                >
                    <template slot-scope="scope">
                        <el-tag
                            type="success"
                            v-if="scope.row.oPstate === 1"
                            ></el-tag
                        >
                        <el-tag
                            type="success"
                            v-if="scope.row.oPriceState === 0 &&scope.row.oPstate === 0"
                            ></el-tag
                        >
                        <!-- <el-tag type="danger" v-if="scope.row.oPriceState === 0 && scope.row.oState === 1"></el-tag> -->
                        <el-button
                            type="warning"
                            icon="iconfont icon-r-mark1"
                            style="font-size: 14px"
                            v-if="
                                scope.row.oPriceState === 1 &&
                                scope.row.oPstate === 0
                            "
                            @click="priceClick2(scope.row.oId, scope.row.otype)"
                        >
                            </el-button
                        >
                    </template>
                </el-table-column> 


                <el-table-column prop="oState" label="" width="100px">
                    <template slot-scope="scope">
                        <el-tag
                            type="success"
                            v-if="
                                scope.row.oState === 1 &&
                                scope.row.oPriceState === 1
                            "
                            ></el-tag
                        >
                        <el-tag
                            type="success"
                            v-if="
                                scope.row.oState === 1 &&
                                scope.row.oPriceState === 0
                            "
                            ></el-tag
                        >
                        <el-tag
                            type="danger"
                            v-if="
                                scope.row.oState === 0 && scope.row.oState === 0
                            "
                            ></el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column label="" width="400px">

                    
                    <template slot-scope="scope">
                        
                        <el-button
                            type="success"
                            icon="iconfont icon-r-find"
                            style="font-size: 14px"
                            @click="seeReport(scope.row.oId, scope.row.otype, scope.row.pId)"
                            v-if="
                                scope.row.oState === 1 
                            "
                            > </el-button
                        >
                      
                        <el-button
                            type="success"
                            icon="iconfont icon-r-find"
                            style="font-size: 14px"
                            @click="seeReport2(scope.row.oId, scope.row.otype, scope.row.pId)"
                            v-if="
                                scope.row.oState === 1 &&
                                scope.row.oPriceState === 1 
                            "
                            > </el-button
                        >
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="starVisible">
            <div>
                <h3>
                    {{ dId }}&nbsp;{{ dName }}&nbsp;
                </h3>
            </div>
            <div>
                <el-rate v-model="star" show-text> </el-rate>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="starVisible = false" style="font-size: 18px;"><i class="iconfont icon-r-left" style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="starClick"style="font-size: 18px;"><i class="iconfont icon-r-yes" style="font-size: 20px;"></i>  </el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import request from "@/utils/request.js";
import { getToken } from "@/utils/storage.js";
import jwtDecode from "jwt-decode";
export default {
    name: "MyOrder",
    data() {
        return {
            userId: 1,
            orderData: [],
            star: 5,
            starVisible: false,
            dId: 1,
            dName: "",
        };
    },
    methods: {
        //
        starClick() {
            console.log(this.star);
            console.log(this.dId);
            request
                .get("doctor/updateStar", {
                    params: {
                        dId: this.dId,
                        dStar: this.star,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        return this.$message.error("");
                    this.$message.success("");
                    this.starVisible = false;
                });
        },
        
        priceClick2(oId,otype){
            if(otype =="P"){
                request
                .get("order/updatePrice2", {
                    params: {
                        oId: oId,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200) {
                        this.$message.error("");
                        return;
                    }
                    this.$message.success(" " + oId + " ");
                   // this.starVisible = true;
                    this.requestOrder();
                });
            }else{
                request
                .get("setmealOrder/updateSetmealOrdersPrice2", {
                    params: {
                        oId: oId,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200) {
                        this.$message.error("");
                        return;
                    }
                    this.$message.success(" " + oId + " ");
            
                   // this.starVisible = true;
                    this.requestOrder();
                });
            }
        },
        //
        priceClick(oId, dId,tid,otype) {
            if(otype =="P"){
                request
                .get("order/updatePrice", {
                    params: {
                        oId: oId,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200) {
                        this.$message.error("");
                        return;
                    }
                    this.$message.success(" " + oId + " ");
                    request
                        .get("admin/findDoctor", {
                            params: {
                                dId: dId,
                            },
                        })
                        .then((res) => {
                            if (res.data.status !== 200)
                                return this.$message.error("");
                            this.dId = res.data.data.dId;
                            this.dName = res.data.data.dName;
                        });
                   // this.starVisible = true;
                    this.requestOrder();
                });
            }else {
             
            request
                        .get("setmealOrder/addOrders", {
                            params: {
                                oId: oId,
                            },
                        })
                        .then((res) => {
                            console.log("res");
                            console.log(res);
                            if (res.data.status == 200)
                            this.$message.success(res.data.msg);

                            if(res.data.msg == ""){
                                //
                                request
                                    .get("setmealOrder/updateSetmealOrdersPrice", {
                                        params: {
                                            oId: oId,
                                        },
                                    })
                                    .then((res) => {
                                        if (res.data.status !== 200) {
                                            this.$message.error("");
                                            return;
                                        }
                                        this.$message.success(" " + oId + " ");
                                        this.requestOrder();

                                    });

                            }

                            this.requestDoctors();
                        })
                        .catch((e) => {
                            console.log(e);
                           
                        });   


            }

            
        },
        //
        requestOrder() {
            request
                .get("patient/findOrderByPid", {
                    params: {
                        pId: this.userId,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        this.$message.error("");
                    this.orderData = res.data.data;
                    //this.orderData.dSection = res.data.data.map(item => item.doctor.dSection);
                    //console.log(res.data.data.map(item => item.doctor.dSection));
                    console.log(this.orderData.oId);
                    console.log(this.orderData.pName);
                    console.log(res);
                });
        },
        //token
        tokenDecode(token) {
            if (token !== null) return jwtDecode(token);
        },
    },
    created() {
        // token
        //this.orderData.pName = this.tokenDecode(getToken()).pName;
        //this.orderData.pCard = this.tokenDecode(getToken()).pCard;
        this.userId = this.tokenDecode(getToken()).pId;
        console.log(this.orderData.pName);
        //this.orderData.pName = "dasda"
        this.requestOrder();
    },
};
</script>
