<script>
import { toLoad } from "@/utils/initialize.js";
import {
    clearToken,
    getActivePath,
    getToken,
    setActivePath,
} from "@/utils/storage.js";
import jwtDecode from "jwt-decode";
export default {
    name: "Admin",
    data() {
        return {
            userName: "",
            activePath: "",
        };
    },
    methods: {
        //token
        tokenDecode(token) {
            if (token !== null) return jwtDecode(token);
        },
        //
        menuClick(path) {
            this.activePath = path;
            setActivePath(path);
            if (this.$route.path !== "/" + path) this.$router.push(path);
            console.log(path);
        },
        //
        logout() {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(() => {
                    clearToken();
                    this.$message({
                        type: "success",
                        message: "!",
                    });
                    this.$router.push("login");
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "",
                    });
                });
        },
    },
    mounted() {
        toLoad();
    },
    created() {
        //  
        this.activePath = getActivePath();
        // token
        this.userName = this.tokenDecode(getToken()).aName;
    },
};
</script>
