<template>
    <div>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row type="flex">
                <el-col :span="6">
                    <el-input v-model="query" placeholder="id">
                        <el-button
                            slot="append"
                            @click="requestBeds"
                            style="font-size: 18px"
                        ><i class="iconfont icon-r-find" style="font-size: 22px;"></i> </el-button>
                    </el-input>
                </el-col>
                <el-col :span="6"></el-col>
                <el-col :span="6">
                    <el-button
                        type="primary"
                        style="font-size: 18px"
                        @click="addFormVisible = true"
                    >
                    <i class="iconfont icon-r-add" style="font-size: 22px;"></i> 
                        </el-button
                    >
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="bedData" stripe style="width: 100%" border>
                <el-table-column label="" prop="bId"></el-table-column>
                <el-table-column label="id" prop="pId"></el-table-column>
                <el-table-column label="id" prop="dId"></el-table-column>
                <el-table-column
                    label=""
                    prop="bStart"
                ></el-table-column>
                <el-table-column
                    label=""
                    prop="bReason"
                ></el-table-column>
                <el-table-column label="" prop="bState">
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.bState === 1" type="danger"
                            ></el-tag
                        >
                        <el-tag v-if="scope.row.bState === 0" type="success"
                            ></el-tag
                        >
                    </template>
                </el-table-column>

                <el-table-column label="" width="240" fixed="right">
                    <template slot-scope="scope">
                        <el-button
                            style="font-size: 18px"
                            type="success"
                            @click="deleteDialog(scope.row.bId)"
                        ><i class="iconfont icon-r-edit" style="font-size: 22px;"></i> </el-button>
                        <el-button
                            style="font-size: 18px"
                            type="danger"
                            @click="emptyDialog(scope.row.bId)"
                        ><i class="iconfont icon-r-delete" style="font-size: 22px;"></i> </el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                background
                layout="total, sizes, prev, pager, next, jumper"
                :current-page="pageNumber"
                :page-size="size"
                :page-sizes="[1, 2, 4, 8, 16]"
                :total="total"
            >
            </el-pagination>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <el-form :model="addForm" :rules="rules" ref="ruleForm">
                <el-form-item label="" prop="bId" label-width="80px">
                    <el-input v-model.number="addForm.bId"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false" style="font-size: 18px;"><i class="iconfont icon-r-left" style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="addBed('ruleForm')"
                    style="font-size: 18px;"><i class="iconfont icon-r-yes" style="font-size: 20px;"></i>  </el-button
                >
            </div>
        </el-dialog>
    </div>
</template>
<script>
import { toLoad } from "@/utils/initialize.js";
import request from "@/utils/request.js";
export default {
    name: "CheckList",
    data() {
        return {
            pageNumber: 1,
            size: 8,
            query: "",
            bedData: [],
            total: 3,
            addFormVisible: false,
            addForm: {},
            rules: {
                bId: [
                    { required: true, message: "", trigger: "blur" },
                    {
                        type: "number",
                        message: "",
                        trigger: "blur",
                    },
                ],
            },
        };
    },
    methods: {
        //
        emptyBed(id) {
            request
                .get("bed/emptyBed", {
                    params: {
                        bId: id,
                    },
                })
                .then((res) => {
                    this.requestBeds();
                    console.log(res);
                });
        },
        //
        emptyDialog(id) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(() => {
                    this.emptyBed(id);
                    this.$message({
                        type: "success",
                        message: "!",
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "",
                    });
                });
        },

        //
        deleteBed(id) {
            request
                .get("bed/deleteBed", {
                    params: {
                        bId: id,
                    },
                })
                .then((res) => {
                    this.requestBeds();
                    console.log(res);
                });
        },
        //
        deleteDialog(id) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(() => {
                    this.deleteBed(id);
                    this.$message({
                        type: "success",
                        message: "!",
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "",
                    });
                });
        },
        //
        addBed(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .get("bed/addBed", {
                            params: {
                                bId: this.addForm.bId,
                                pId: -1,
                                dId: -1,
                            },
                        })
                        .then((res) => {
                            if (res.data.status !== 200)
                                return this.$message.error("");
                            this.addFormVisible = false;
                            this.requestBeds();
                            this.$message.success("");
                            console.log(res);
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        handleSizeChange(size) {
            this.size = size;
            this.requestBeds();
        },
        //   
        handleCurrentChange(num) {
            console.log(num);
            this.pageNumber = num;
            this.requestBeds();
        },
        // 
        requestBeds() {
            request
                .get("bed/findAllBeds", {
                    params: {
                        pageNumber: this.pageNumber,
                        size: this.size,
                        query: this.query,
                    },
                })
                .then((res) => {
                    this.bedData = res.data.data.beds;
                    this.total = res.data.data.total;
                    toLoad()
                });
        },
    },
    created() {
        this.requestBeds();
    },
};
</script>
<style scoped lang="scss">
.el-table {
    margin-top: 20px;
    margin-bottom: 20px;
}
.el-form {
    margin-top: 0;
}
</style>