                            ></el-tag
                        >
                        <!-- <el-tag type="danger" v-if="scope.row.oPriceState === 0 && scope.row.oState === 1"></el-tag> -->
                        <el-button
                            type="warning"
                            icon="iconfont icon-r-mark1"
                            style="font-size: 14px"
                            v-if="
                                scope.row.oPriceState === 0 &&
                                scope.row.oState === 1
                            "
                            @click="pClick(scope.row.oId, scope.row.tid)"
                        >
                            </el-button
                        >
                    </template>
                </el-table-column>
                
                <el-table-column prop="oState" label="" >
                    <template slot-scope="scope">
                        <el-tag
                            type="success"
