                            "
                            @click="priceClick(
                                scope.row.pId, 
                                scope.row.tid,
                                scope.row.oStart,
                                scope.row.tname,
                                scope.row.oTotalPrice,
                                scope.row.tid,
                                scope.row.tid,
                            )"
                        >
                            </el-button
                        >
                    </template>
                </el-table-column>
                
            </el-table>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="starVisible">
            <el-form :model="orderForm" ref="orderForm" :rules="orderRules">
                <el-form-item
                    label=""
                    label-width="100px"
                    prop="oTime"
                >
                    <el-select
                        v-model="orderForm.oTime"
                        placeholder=""
                        no-data-text=""
                    >
                        <el-option
                            v-for="time in times"
                            :key="time"
                            :label="time"
                            :value="time"
                        >
                        </el-option>
                    </el-select>
                </el-form-item>
                
