<template>
    <div>
        <el-card>
            <!--  -->
            <el-row type="flex">
                <el-col :span="6">
                    <el-input v-model="query" placeholder="id">
                        <el-button
                            slot="append"
                            style="font-size: 18px;"
                            @click="requestOrders"
                        ><i class="iconfont icon-r-find" style="font-size: 22px;"></i> </el-button>
                    </el-input>
                </el-col>
            </el-row>
            <el-table :data="orderData" stripe style="width: 100%" border>
                <el-table-column
                    prop="oId"
                    label=""
                    width="80px"
                ></el-table-column>
                <el-table-column
                    prop="dId"
                    label="id"
                    width="80px"
                ></el-table-column>

                <el-table-column prop="pId" label="id" width="100px">
                </el-table-column>

                <el-table-column
                    prop="oStart"
                    label=""
                    width="190px"
                ></el-table-column>
                <el-table-column
                    prop="oEnd"
                    label=""
                    width="180px"
                ></el-table-column>
                <el-table-column
                    prop="oRecord"
                    label=""
                    width="400px"
                ></el-table-column>
                <el-table-column
                    prop="oDrug"
                    label=""
                    width="180px"
                ></el-table-column>
                <el-table-column
                    prop="oCheck"
                    label=""
                    width="180px"
                ></el-table-column>
                <el-table-column
                    prop="oTotalPrice"
                    label="/"
                    width="80px"
                ></el-table-column>
                <el-table-column
                    prop="oPriceState"
                    label=""
                    width="100px"
                >
                    <template slot-scope="scope">
                        <el-tag
                            type="success"
                            v-if="scope.row.oPriceState === 1"
                            ></el-tag
                        >
                        <!-- <el-tag type="danger" v-if="scope.row.oPriceState === 0 && scope.row.oState === 1"></el-tag> -->
                        <el-tag
                            type="danger"
                            v-if="
                                scope.row.oPriceState === 0 &&
                                scope.row.oState === 1
                            "
                            ></el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column prop="oState" label="" width="100px">
                    <template slot-scope="scope">
                        <el-tag
                            type="success"
                            v-if="
                                scope.row.oState === 1 &&
                                scope.row.oPriceState === 1
                            "
                            ></el-tag
                        >
                        <el-tag
                            type="danger"
                            v-if="
                                scope.row.oState === 0 && scope.row.oState === 0
                            "
                            ></el-tag
                        >
                    </template>
                </el-table-column>
                <el-table-column label="" width="140" fixed="right">
                    <template slot-scope="scope">
                        <el-button
                            type="warning"
                            style="font-size: 18px"
                            @click="dealClick(scope.row.oId, scope.row.pId)"
                            v-if="
                                scope.row.oState === 1 &&
                                scope.row.oPriceState === 1
                            "
                            ><i class="iconfont icon-r-yes" style="font-size: 22px;"></i> </el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                background
                layout="total, sizes, prev, pager, next, jumper"
                :current-page="pageNumber"
                :page-size="size"
                :page-sizes="[1, 2, 4, 8, 16]"
                :total="total"
            >
            </el-pagination>
        </el-card>
    </div>
</template>
<script>
import { toLoad } from "@/utils/initialize.js";
import request from "@/utils/request.js";
import { getToken } from "@/utils/storage.js";
import jwtDecode from "jwt-decode";
export default {
    name: "DoctorOrder",
    data() {
        return {
            userId: 1,
            orderData: [],
            pageNumber: 1,
            size: 8,
            query: "",
            total: 3,
        };
    },
    methods: {
        //
        dealClick(oId, pId) {
            this.$router.push({
                path: "/dealOrderAgain",
                query: {
                    oId: oId,
                    pId: pId,
                },
            });
        },
        //
        handleSizeChange(size) {
            console.log(size);
            this.size = size;
            this.requestOrders();
        },
        //   
        handleCurrentChange(num) {
            console.log(num);
            this.pageNumber = num;
            this.requestOrders();
        },

        //
        requestOrders() {
            request
                .get("order/findOrderByDid", {
                    params: {
                        dId: this.userId,
                        pageNumber: this.pageNumber,
                        size: this.size,
                        query: this.query,
                    },
                })
                .then((res) => {
                    toLoad()
                    if (res.data.status !== 200)
                        this.$message.error("");
                    this.orderData = res.data.data.orders;
                    this.total = res.data.data.total;

                });
        },
        //token
        tokenDecode(token) {
            if (token !== null) return jwtDecode(token);
        },
    },
    created() {
        // token
        //this.orderData.pName = this.tokenDecode(getToken()).pName;
        //this.orderData.pCard = this.tokenDecode(getToken()).pCard;
        this.userId = this.tokenDecode(getToken()).dId;
        console.log(this.orderData.pName);
        //this.orderData.pName = "dasda"
        this.requestOrders();
    },
};
</script>
<style lang="scss" scoped>
.el-table {
    margin-top: 20px;
    margin-bottom: 20px;
}
</style>