<template>
    <!--  -->
    <el-card>
        <!--  -->
        <el-row type="flex">
            <el-col :span="6">
                <el-input v-model="query" placeholder="">
                    <el-button
                        slot="append"
                        style="font-size: 18px;"
                        @click="requestPatients"
                    ><i class="iconfont icon-r-find" style="font-size: 22px;"></i> </el-button>
                </el-input>
            </el-col>
        </el-row>
        <!--  -->
        <el-table :data="patientData" stripe style="width: 100%" border>
            <el-table-column prop="pId" label="" width="100">
            </el-table-column>
            <el-table-column prop="pName" label="" width="80">
            </el-table-column>
            <el-table-column prop="pGender" label="" width="60">
            </el-table-column>
            <el-table-column prop="pAge" label="/" width="180">
            </el-table-column>
            <el-table-column prop="pCard" label=""> </el-table-column>
            <el-table-column prop="pPhone" label=""> </el-table-column>
            <el-table-column prop="pEmail" label="" width="170">
            </el-table-column>
            <el-table-column prop="pType" label="" width="170"></el-table-column>
            <el-table-column prop="pState" label="" width="80">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.pState === 1"
                        ></el-tag
                    >
                    <el-tag type="danger" v-else></el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" width="160" fixed="right">
                <template slot-scope="scope">
                    <el-button
                        style="font-size: 18px"
                        type="danger"
                        @click="deleteDialog(scope.row.pId)"
                    ><i class="iconfont icon-r-delete" style="font-size: 22px;"></i> </el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            layout="total, sizes, prev, pager, next, jumper"
            :current-page="pageNumber"
            :page-size="size"
            :page-sizes="[1, 2, 4, 8, 16]"
            :total="total"
        >
        </el-pagination>
    </el-card>
</template>
<script>
import { toLoad } from "@/utils/initialize.js";
import request from "@/utils/request.js";
export default {
    name: "PatientList",
    data() {
        return {
            pageNumber: 1,
            size: 8,
            query: "",
            patientData: [],
            total: 3,
        };
    },
    methods: {
        //
        deletePatient(id) {
            request
                .get("admin/deletePatient", {
                    params: {
                        pId: id,
                    },
                })
                .then((res) => {
                    this.requestPatients();
                    console.log(res);
                });
        },
        //
        deleteDialog(id) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(() => {
                    this.deletePatient(id);
                    this.$message({
                        type: "success",
                        message: "!",
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "",
                    });
                });
        },
        //
        handleSizeChange(size) {
            this.size = size;
            this.requestPatients();
        },
        //   
        handleCurrentChange(num) {
            console.log(num);
            this.pageNumber = num;
            this.requestPatients();
        },
        // 
        requestPatients() {
            request
                .get("admin/findAllPatients", {
                    params: {
                        pageNumber: this.pageNumber,
                        size: this.size,
                        query: this.query,
                    },
                })
                .then((res) => {
                    this.patientData = res.data.data.patients;

                    this.total = res.data.data.total;
                    toLoad()
                });
        },
    },
    created() {
        this.requestPatients();
    },
};
</script>
<style scoped lang="scss">
.el-table {
    margin-top: 20px;
    margin-bottom: 20px;
}
.el-form {
    margin-top: 0;
}
</style>