<template>
    <!--  -->
    <el-card>
        <!--  -->
        <el-row type="flex">
            
            <el-col :span="4"></el-col>
            <el-col :span="4">
                <el-button type="primary" @click="addFormVisible = true"
                        style="font-size: 18px;"
                    >
                    <i class="iconfont icon-r-add" style="font-size: 22px;"></i> 
                    </el-button
                >
            </el-col>
            <el-col :span="4"></el-col>
            <!--  -->
            <!-- <el-col :span="2">
                <el-upload
                    class="upload-demo"
                    action="doctor/uploadExcel"
                    accept=".xlsx,.xls "
                    :limit="1"
                    :show-file-list="false"
                    :on-progress="handleProgress"
                    :on-exceed="handleExceed"
                    :on-success="handleSuccess"
                    :on-error="handleError"
                    :file-list="fileList"
                >
                    <el-button size="small" type="success"></el-button>
                </el-upload>
            </el-col>

            <el-col :span="2">
                <el-button size="small" type="success" @click="exportDoctors"
                    ></el-button
                ></el-col
            > -->
        </el-row>
        <!--  -->
        <el-table :data="subjectData" stripe style="width: 100%" border>
            <el-table-column prop="kid" label="id">
            </el-table-column>
            <el-table-column prop="name" label="1" >
            </el-table-column>
            <el-table-column prop="kname" label="2" >
            </el-table-column>
            <el-table-column prop="sert" label="" >
            </el-table-column>
           
            <el-table-column label=""  fixed="right">
                <template slot-scope="scope">
                    <el-button
                        style="font-size: 18px;"
                        type="success"
                        @click="modifyDialog(scope.row.kid)"
                    ><i class="iconfont icon-r-edit" style="font-size: 22px;"></i> </el-button>
                    <el-button
                        style="font-size: 18px;"
                        type="danger"
                        @click="deleteDialog(scope.row.kid)"
                    ><i class="iconfont icon-r-delete" style="font-size: 22px;"></i> </el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            layout="total, sizes, prev, pager, next, jumper"
            :current-page="pageNumber"
            :page-size="size"
            :page-sizes="[1, 2, 4, 8, 16]"
            :total="total"
        >
        </el-pagination>
        <!--  -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <el-form :model="addForm" :rules="rules" ref="ruleForm">
                <el-form-item label="1" label-width="80px" prop="name">
                    <el-select v-model="addForm.name" placeholder="1">
                        <el-option
                            v-for="post in posts"
                            :key="post"
                            :label="post"
                            :value="post"
                        >
                        </el-option>
                    </el-select>
                </el-form-item>   
                <el-form-item label="2" label-width="80px" prop="kname">
                    <el-input
                        v-model.number="addForm.kname"
                        autocomplete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="sert">
                    <el-input
                        v-model.number="addForm.sert"
                        autocomplete="off"
                    ></el-input>
                </el-form-item>
               
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false" style="font-size: 18px;"><i class="iconfont icon-r-left" style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="addSubject('ruleForm')"
                    style="font-size: 18px;"><i class="iconfont icon-r-yes" style="font-size: 20px;"></i>  </el-button
                >
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="modifyFormVisible">
            <el-form :model="modifyForm" :rules="rules" ref="ruleForm">

            <el-form-item label="1" label-width="80px" prop="name">
                    <el-select
                        v-model="modifyForm.name"
                        placeholder="1"
                    >
                        <el-option
                            v-for="post in posts"
                            :key="post"
                            :label="post"
                            :value="post"
                        >
                        </el-option>
                    </el-select>
                </el-form-item> 

                <el-form-item label="2" label-width="80px" prop="kname">
                    <el-input
                        v-model="modifyForm.kname"
                        autocomplete="off"
                    ></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="sert">
                    <el-input
                        v-model="modifyForm.sert"
                        autocomplete="off"
                    ></el-input>
                </el-form-item>
               
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modifyFormVisible = false" style="font-size: 18px;"><i class="iconfont icon-r-left" style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="modifyDoctor('ruleForm')"
                    style="font-size: 18px;"><i class="iconfont icon-r-yes" style="font-size: 20px;"></i>  </el-button
                >
            </div>
        </el-dialog>
    </el-card>
</template>
<script>
import request from "@/utils/request.js";
import { toLoad } from "@/utils/initialize.js";
export default {
    name: "SubjectList",
    data() {
       
        return {
            pageNumber: 1,
            size: 8,
            query: "",
            total: 3,
            addFormVisible: false,
            modifyFormVisible: false,
            subjectData:[],
            addForm: {},
            modifyForm: {},
            posts: ["", "", "", "", "", "", ""],
            rules: {
                name: [
                    { required: true, message: "1", trigger: "blur" },
                    {
                        min: 2,
                        max: 10,
                        message: "210",
                        trigger: "blur",
                    },
                ],
                kname: [
                    { required: true, message: "2", trigger: "blur" },
                    {
                        min: 2,
                        max: 10,
                        message: "210",
                        trigger: "blur",
                    },
                ],
                sert: [
                    { required: true, message: "", trigger: "blur" },
                ],
            }    
        };
    },
    methods: {
    //
        modifyDoctor(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .get("subject/modifySubject", {
                            params: {
                                kid: this.modifyForm.kid,
                                name: this.modifyForm.name,
                                kname: this.modifyForm.kname,
                                sert: this.modifyForm.sert
                               
                            },
                        })
                        .then((res) => {
                            if (res.data.status !== 200)
                                return this.$message.error("");
                            this.modifyFormVisible = false;
                            this.requestDoctors();
                            this.$message.success("");
                            console.log(res);
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },

        //
        modifyDialog(id) {
            request
                .get("subject/findSubject", {
                    params: {
                        kid: id,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        this.$message.error("");
                    this.modifyForm = res.data.data;
                    this.modifyFormVisible = true;
                    console.log(res);
                });
        },
         //
         deleteSubject(id) {
            request
                .get("subject/deleteSubject", {
                    params: {
                        kid: id,
                    },
                })
                .then((res) => {
                    this.requestDoctors();
                    console.log(res);
                });
        },
      
        //
        deleteDialog(id) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(() => {
                    this.deleteSubject(id);
                    this.$message({
                        type: "success",
                        message: "!",
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "",
                    });
                });
        },
        // 
        addSubject(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .get("subject/addSubject", {
                            params: {
                                name: this.addForm.name,
                                kname: this.addForm.kname,
                                sert:this.addForm.sert
                            },
                        })
                        .then((res) => {
                            if (res.data.status !== 200)
                                return this.$message.error(
                                    ""
                                );
                            this.addFormVisible = false;
                            this.requestDoctors();
                            this.$message.success("");
                            console.log(res);
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        handleSizeChange(size) {
            this.size = size;
            this.requestDoctors();
        },
        //   
        handleCurrentChange(num) {
            console.log(num);
            this.pageNumber = num;
            this.requestDoctors();
        },
        // 
        requestDoctors() {
            request
                .get("subject/findAllSubjects", {
                    params: {
                        pageNumber: this.pageNumber,
                        size: this.size,
                        query: this.query,
                    },
                })
                .then((res) => {
                    this.subjectData = res.data.data.subjects;

                    this.total = res.data.data.total;
                    toLoad()
                });
        },
    },
    created() {
        this.requestDoctors();
    },
};
</script>
<style scoped lang="scss">
.el-table {
    margin-top: 20px;
    margin-bottom: 20px;
}
.el-form {
    margin-top: 0;
}
</style>